import { createRouter, createWebHashHistory } from 'vue-router';

const routes = [
  {
    path: '/',
    name: 'home',
    component: () => import('../views/HomeView.vue'),
    children: [
      {
        path: '/',
        name: 'index',
        component: () => import('../views/front/IndexView.vue'),
      },
      {
        path: 'homework',
        name: 'homework',
        component: () => import('../views/front/HomeWork.vue'),
        children: [
          {
            path: 'Day-01',
            name: 'day1',
            component: () => import('@/components/homework/DayOne.vue'),
          },
          {
            path: 'Day-02',
            name: 'day2',
            component: () => import('@/components/homework/DayTwo.vue'),
          },
        ],
      },
      {
        path: 'user',
        name: 'user',
        component: () => import('../views/front/UserView.vue'),
      },
      {
        path: 'prepa',
        name: '課前預習',
        component: () => import('../views/front/prepaView.vue'),
      },
    ],
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

export default router;
